import React, { useEffect, useRef, useState } from 'react';
import '../opghegedusic.css';
import { useLocation } from 'react-router-dom';

function Home() {
  const [isAnimated, setIsAnimated] = useState(false);
  const [showContent, setShowContent] = useState(false);
  const location = useLocation();
  const firstLoad = useRef(true); // Ref za praenje prvog uitavanja

  useEffect(() => {
    // Provjera je li stranica prvi put uitana ili je dolo do refresh-a
    if (firstLoad.current) {
      // Ako je stranica prvi put uitana ili osvjeena
      setIsAnimated(true); // Pokreemo animaciju
      firstLoad.current = false; // Nakon prvog uitavanja, postavljamo na false
    } else {
      setShowContent(true); // Ako je ve bila uitana, odmah prikazujemo sadraj
    }
  }, [location]); // Praenje promjena URL-a pomou useLocation hooka

  return (
    <>
    <div className={`home-page ${isAnimated ? 'fadeIn' : ''}`} style={{ opacity: showContent || isAnimated ? 1 : 0 }}>
          <img src="https://i.ibb.co/Fk2SYZBy/ljesnjak.jpg" alt="ljesnjak" border="0" className='bannerImg'/>
          <h2>
    <strong>OPG Hegedui </strong> <br/>
    </h2>
    <p>
     Kvalitetan uzgoj ljenjaka s ljubavlju i panjom
Dobrodoli na slubenu stranicu OPG-a Hegedui, obiteljskog poljoprivrednog gospodarstva specijaliziranog za uzgoj ljenjaka. Naa obitelj dugi niz godina njeguje tradiciju i strast prema poljoprivredi, a na primarni cilj je pruiti vam visokokvalitetne, svjee i zdrave plodove koji dolaze iz naeg vlastitog uzgoja.
</p>
<h4>
  <strong>
Kvaliteta koja dolazi iz prirode
</strong>
</h4>
<p>
 <br/>
Ljenjak je vie od obinog ploda  to je simbol zdravlja, kvalitete i tradicije. Naa plantaa ljenjaka smjetena je u srcu prirode, gdje u sinergiji s okoliem njegujemo svaki korak uzgoja. Na OPG-u Hegedui posveeni smo odrivom uzgoju, bez upotrebe kemijskih sredstava, kako bismo osigurali da nai ljenjaci budu najkvalitetniji i ekoloki prihvatljivi.
 <br/>
 <strong><i>
Zato odabrati nas?
</i></strong>
<br/>
Prirodni uzgoj  Nai ljenjaci rastu u zdravom, kontroliranom okruenju, daleko od pesticida i kemikalija.
Obiteljska tradicija  Posveeni smo ouvanju obiteljske tradicije i kvalitetnog poljoprivrednog rada.
Svjeina i kvaliteta  Svaki ljenjak koji doe do vas, proao je rigoroznu kontrolu kvalitete kako bi osigurao najbolje plodove na tritu.
Odrivo gospodarstvo  Sposobni smo proizvoditi na nain koji uva okoli i pridaje vanost ouvanju prirodnih resursa.
</p>
<h4>
  <strong>
Naa misija
</strong>
</h4>
<p>
Naa misija je obogatiti trite najboljim ljenjacima, koji nisu samo ukusni, ve i bogati hranjivim tvarima. elimo pruiti proizvode koji ine razliku i potiu zdravlje naih kupaca, uz ouvanje prirodnih vrijednosti naeg kraja.
</p> 
        
    </div>
    
    </>
  );
};

export default Home;
